import Polyline from './Polyline';
import util from '../../util/util';
import bezier from '../../math/bezier';
// import { union, diff, intersection, xor } from '../../math/martinez';
import bo from '../../math/bo/index';

let { intersect, union, subtract, subtract2, xor, chain } = bo;

// 根据曲线长度将其分割为细小的曲线段，每个曲线段可近似认为是直线段，从而参与布尔运算
function convertCurve2Line(poly) {
  for(let len = poly.length, i = len - 1; i >= 0; i--) {
    let cur = poly[i], len2 = cur.length;
    if(len2 === 4 || len2 === 6) {
      let last = poly[(i || len) - 1];
      let len3 = last.length;
      let lastX = last[len3 - 2], lastY = last[len3 - 1];
      let coords = [
        [lastX, lastY],
        [cur[0], cur[1]],
        [cur[2], cur[3]],
      ];
      if(cur.length === 6) {
        coords.push([cur[4], cur[5]]);
      }
      let l = bezier.bezierLength(coords);
      // 每2个px长度分割
      let n = Math.ceil(l * 0.2);
      // <2px直接返回直线段
      if(n === 1) {
        cur.splice(0, len2 - 2);
      }
      else {
        // n段每段t为per
        let per = 1 / n;
        for(let j = 1; j < n; j++) {
          let p = bezier.pointAtByT(coords, per * j);
          poly.splice(i + j - 1, 0, p);
        }
        // 原本的曲线直接改数据为最后一段截取的
        cur.splice(0, len2 - 2);
      }
    }
  }
}

class Polygon extends Polyline {
  constructor(tagName, props) {
    super(tagName, props);
    if(props.booleanOperations) {
      this.__booleanOperations = props.booleanOperations;
    }
  }

  __getPoints(originX, originY, width, height, points, isControl) {
    let res = super.__getPoints(originX, originY, width, height, points, isControl);
    if(!isControl) {
      res.push(res[0]);
    }
    return res;
  }

  // 布尔运算覆盖，仅multi才发生，因为需要多个多边形数据
  __reprocessing(list, isMulti) {
    if(!isMulti || list.length < 2) {
      return list;
    }
    let bo = this.booleanOperations, len = list.length;
    if(!Array.isArray(bo) && bo) {
      let old = bo;
      bo = [bo];
      for(let i = 1; i < len - 1; i++) {
        bo.push(old);
      }
    }
    if(Array.isArray(bo) && bo.length) {
      // list.forEach(poly => {
      //   if(poly && poly.length > 1) {
      //     convertCurve2Line(poly);
      //   }
      // });
      // 输出结果，以及连续多个中可能出现无布尔运算的多边形，此时中断运算存储结果，后续从头开始
      let res = [], temp = list[0];
      for(let i = 1; i < len; i++) {
        let op = (bo[i - 1] || '').toString().toLowerCase();
        let cur = list[i];
        if(!cur || cur.length < 2) {
          if(temp) {
            res = res.concat(chain(temp));
          }
          temp = [];
          continue;
        }
        if(['intersect', 'intersection', 'union', 'subtract', 'subtract2', 'diff', 'difference', 'xor'].indexOf(op) === -1) {
          res = res.concat(chain(temp));
          temp = cur; // 以备后面可能的布尔运算
          continue;
        }
        switch(op) {
          case 'intersect':
          case 'intersection':
            temp = intersect(temp, cur, true);
            // let r1 = intersection(res, [cur]);
            // if(r1) {
            //   res = [];
            //   r1.forEach(item => {
            //     res = res.concat(item);
            //   });
            // }
            // else {
            //   res = [];
            // }
            break;
          case 'union':
            temp = union(temp, cur, true);
            // let r2 = union(res, [cur]);
            // if(r2) {
            //   res = [];
            //   r2.forEach(item => {
            //     res = res.concat(item);
            //   });
            // }
            // else {
            //   res = [];
            // }
            break;
          case 'subtract':
          case 'diff':
          case 'difference':
            temp = subtract(temp, cur, true);
            // let r3 = diff(res, [cur]);
            // if(r3) {
            //   res = [];
            //   r3.forEach(item => {
            //     res = res.concat(item);
            //   });
            // }
            // else {
            //   res = [];
            // }
            break;
          case 'subtract2':
            temp = subtract2(temp, cur, true);
            break;
          case 'xor':
            temp = xor(temp, cur, true);
            // let r4 = xor(res, [cur]);
            // if(r4) {
            //   res = [];
            //   r4.forEach(item => {
            //     res = res.concat(item);
            //   });
            // }
            // else {
            //   res = [];
            // }
            break;
        }
      }
      return res.concat(chain(temp));
    }
    return list;
  }

  // 覆盖，当booleanOperations动画改变时刷新顶点缓存
  __needRebuildSE(__cacheProps) {
    if(util.isNil(__cacheProps.booleanOperations)) {
      __cacheProps.booleanOperations = true;
      return true;
    }
  }

  get booleanOperations() {
    return this.getProps('booleanOperations');
  }
}

export default Polygon;
